@import 'Styles/theme.scss';

.login {
    display        : flex;
    justify-content: center;
    align-items    : center;
    margin         : 132px auto;
    width          : 1200px;
    height         : 700px;
    background     : url('../../Assets/login-bg.png') no-repeat;

    .login-box {
        width          : 840px;
        height         : 498px;
        background     : $white;
        border-radius  : 10px;
        box-shadow     : 0px 0px 30px 0px rgba(96, 128, 180, 0.50);
        margin         : 80px 108px 120px 45px;
        display        : flex;
        justify-content: center;
        position       : relative;

        .title {
            position   : absolute;
            color      : $darkBlue;
            font-weight: W6;
            left       : 20px;
            top        : 20px;
            font-size  : 22px;
            font-family: HiraginoSansGB, HiraginoSansGB-Regular;
        }

        .login-item-box {
            display        : flex;
            justify-content: center;
            align-items    : center;
            font-size      : 14px;

            .login-item {
                width         : 268px;
                height        : 268px;
                border        : 1px solid rgba(96, 128, 180, 0.50);
                border-radius : 10px;
                box-shadow    : 0px 0px 20px 0px rgba(96, 128, 180, 0.20);
                cursor        : pointer;
                display       : flex;
                align-items   : center;
                flex-direction: column;

                &:first-child {
                    margin-right: 40px;
                }

                .login-item-logo {
                    padding-top: 45px;
                }

                .login-item-title {
                    padding    : 16px 0 14px;
                    font-size  : 18px;
                    font-family: HiraginoSansGB, HiraginoSansGB-Regular;
                    font-weight: 600;
                    color      : rgba(0, 0, 0, 0.80);
                }

                .login-item-desc {
                    width     : 185px;
                    text-align: center;
                }
            }
        }
    }
}